export default{
	name:'matter',
	props:{
		imgArr:Array,
		video:String
	},	
	data(){
		return{
			videoing:false
		}
	},
	onLoad() {
		uni.showToast({
			icon:'../../../static/loading.svg'
		})
	},
	watch:{
		video(newValue,oldValue){
			if(newValue.video != ''){
				this.videoing = true;
			}
		}
	}
}
